<template>
  <aside class="app-sidebar" :class="'app-sidebar--' + sidebarLayoutSkin">
    <div class="app-sidebar__inner">
      <el-menu
        :collapse="sidebarFold"
        :collapseTransition="false"
        :default-active="menuActiveName || 'home'"
        class="app-sidebar__menu"
      >

        <el-menu-item index="order" @click="$router.push({ name: 'order' })">
          <icon name="order" class="app-sidebar__menu-icon" :width="18" :height="18"></icon>
          <span slot="title">商品订单</span>
        </el-menu-item>
        <el-menu-item index="withdraw" @click="$router.push({ name: 'withdraw' })">
          <icon name="withdraw" class="app-sidebar__menu-icon" :width="18" :height="18"></icon>
          <span slot="title">提现地址</span>
        </el-menu-item>
        <el-menu-item index="wallet" @click="$router.push({ name: 'wallet' })">
          <icon name="wallet" class="app-sidebar__menu-icon" :width="18" :height="18"></icon>
          <span slot="title">钱包</span>
        </el-menu-item>

      </el-menu>
    </div>
  </aside>
</template>
<script>
export default {
  name: "AppSidebar",
  computed: {
    sidebarFold: {
      get() {
        return this.$store.state.common.sidebarFold;
      }
    },
    sidebarLayoutSkin: {
      get() {
        return this.$store.state.common.sidebarLayoutSkin;
      }
    },
    menuActiveName: {
      get() {
        return this.$store.state.common.menuActiveName;
      },
      set(val) {
        this.$store.commit("common/updateMenuActiveName", val);
      }
    }
  }
};
</script>
