<template>
  <div class="app-content" :class="{ 'app-content--tabs': $route.meta.isTab }">
    <el-card :body-style="appContentViewHeight">
      <keep-alive> <router-view /> </keep-alive>
    </el-card>
  </div>
</template>
<script>
import { isURL } from "@/utils/validate";
export default {
  name: "AppContent",
  data() {
    return {};
  },
  computed: {
    documentClientHeight: {
      get() {
        return this.$store.state.common.documentClientHeight;
      }
    },
    appContentViewHeight() {
      let height = this.documentClientHeight - 50 - 30 - 2;
      if (this.$route.meta.isTab) {
        height -= 40;

        return {
          [isURL(this.$route.meta.iframeUrl) ? "height" : "min-height"]:
            height + "px"
        };
      }

      return {
        "min-height": height + "px"
      };
    }
  }
};
</script>
