<template>
  <div class="app-navbar" :class="{ 'app-aside--flod': navbarLayoutType }">
    <section class="app-navbar__header">
      <h1 class="app-navbar__logo" @click="$router.push({ name: 'home' })">
        <span class="app-logo app-logo--large">链优品 商户后台</span>
        <span class="app-logo app-logo--mini">链优品</span>
      </h1>
    </section>
    <section class="app-navbar__body clearfix">
      <div class="app-navbar__switch" @click="sidebarFold = !sidebarFold">
        
      </div>
      <el-menu mode="horizontal" class="app-navbar__menu">
        <!-- <el-menu-item
          index="1"
          @click="$router.push({ name: 'theme' })"
        >
          <el-badge value="9">
            <i class="el-icon-message"></i>
          </el-badge>
        </el-menu-item> -->
        <el-menu-item index="3" class="app-navbar__user-info">
          <el-dropdown :show-timeout="0" placement="bottom">
            <div class="u-userbox">
              <img
                :alt="userName"
                class="u-userbox__avatar"
                src="~@/assets/img/avatar.png"
              />
              <span class="u-userbox__name">{{ userName }}</span>
            </div>

            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item @click.native="updateUserHandle()">修改用户信息</el-dropdown-item>
              <el-dropdown-item @click.native="updatePasswordHandle()">修改密码</el-dropdown-item>
              <el-dropdown-item @click.native="logoutHandle()">退出</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </el-menu-item>
      </el-menu>
    </section>
  </div>
</template>
<script>
import Vue from "vue";
export default {
  name: "AppNavBar",
  computed: {
    navbarLayoutType: {
      get() {
        return this.$store.state.common.navbarLayoutType;
      }
    },
    sidebarFold: {
      get() {
        return this.$store.state.common.sidebarFold;
      },
      set(val) {
        this.$store.commit("common/updateSidebarFold", val);
      }
    },
    userName: {
      get() {
        return this.$store.state.user.name;
      }
    }
  },
  methods: {
    updateUserHandle() {
      this.$router.push({ name: "user" });
    },
    updatePasswordHandle() {
      this.$router.push({ name: "password" });
    },
    logoutHandle() {
      this.$confirm(`确定进行[退出]操作?`, "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          Vue.cookie.delete("token");
          this.$router.push({ name: "login" });
        })
        .catch(() => {});
    }
  }
};
</script>
